.main{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    .main_section{
        width: 96%;
        height: auto;
        background-color: white;
        border-radius: 5px;
        box-shadow: 0px 0px 5px #ccc;
        padding-bottom: 26px;
        .main_section-h1{
            margin: 16px 0px 14px 12px;
            font-family: alveca;
            font-size: 26px;
        }
        .main_section-article{
            width: 94%;
            margin: 0px auto;
            margin-bottom: 22px;
            .main_article-p-i{
                margin-right: 6px;
                margin-left: 2px;
                font-size: 18px;
            }
            .main_article-p-h2{
                display: inline-block;
                font-size: 18px;
            }
            .main_article-p-span{
                display: block;
                color: gray;
            }
        }
        
    }

}